"use client";

import { Button } from "@/components/ui/button";
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
  CommandList,
} from "@/components/ui/command";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { cn } from "@/lib/utils";
import { Check, ChevronsUpDown } from "lucide-react";
import { useState } from "react";

interface OptionType {
  label: string;
  value: string;
}

export type FilterField = {
  label: string;
  name: string;
  type: "text" | "select" | "date";
  options?: OptionType[]; // only for 'select'
};

type Props = {
  config: FilterField[];
  onFilterChange: (filters: Record<string, string>) => void;
  generateReport: () => void;
};

export default function FilterBar({
  config,
  onFilterChange,
  generateReport,
}: Props) {
  const [filters, setFilters] = useState<Record<string, string>>({});
  const [openField, setOpenField] = useState<string | null>(null);

  const handleChange = (name: string, value: string) => {
    setFilters((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onFilterChange(filters);
  };

  return (
    <div className="w-full flex items-center justify-between bg-white px-4 py-2 border-b">
      <form onSubmit={handleSubmit} className="flex flex-wrap gap-4 items-end">
        {config.map(({ label, name, type, options }) => (
          <div key={name} className="relative min-w-[200px]">
            <label className="absolute left-2 -top-3 bg-white text-sm px-1 text-gray-600">
              {label}
            </label>

            {type === "select" ? (
              <div className="relative">
                <Select
                  value={filters[name] || ""}
                  onValueChange={(value) => handleChange(name, value)}
                >
                  <SelectTrigger className="pr-8">
                    <SelectValue placeholder="All" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="">All</SelectItem>
                    {options?.map(({ label, value }) => (
                      <SelectItem key={value} value={value}>
                        {label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>

                {filters[name] && (
                  <button
                    type="button"
                    onClick={() => handleChange(name, "")}
                    className="absolute right-2 top-1/2 -translate-y-1/2 text-gray-500 hover:text-red-600"
                  >
                    ✕
                  </button>
                )}
              </div>
            ) : (
              <div className="relative">
                <Input
                  type={type}
                  value={filters[name] || ""}
                  onChange={(e) => handleChange(name, e.target.value)}
                  className="pr-8"
                />
                {filters[name] && (
                  <button
                    type="button"
                    onClick={() => handleChange(name, "")}
                    className="absolute right-2 top-1/2 -translate-y-1/2 text-gray-500 hover:text-red-600"
                  >
                    ✕
                  </button>
                )}
              </div>
            )}
          </div>
        ))}

        <Button type="submit" className="bg-primary hover:bg-primary/90">
          Apply
        </Button>
      </form>

      <Button variant="secondary" className="ml-4" onClick={generateReport}>
        Generate Report
      </Button>
    </div>
  );
}
